<template>
  <div class="list" ref="wrapper">
    <div>
      <div class="area" v-for="key in keys" :key="key" :ref="key">
        <div class="title border-topbottom">{{key}}</div>
        <div class="item-list">
          <div
            class="item border-bottom"
            v-for="inneritem of cities[key]"
            :key="inneritem.id"
            @click="handleCityClick(inneritem.user)"
          >
                <img slot="left" class="item-img" :src="inneritem.imgUrl"  style="border-radius: 15px">
                <span slot="left" class="item-title" style="margin-left: 10px">{{inneritem.name}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Bscroll from "better-scroll";
import { mapState,mapMutations } from "vuex";
export default {
  name: "CityList",
  props: {
    cities: Object,
    letter: String
  },
  computed: {
    // 把vuex里的数据映射到该组件的computed计算属性里
    // ...mapState(["city"])
    ...mapState({
    //   currentCity:'city'
    }),
    keys(){
      return Object.keys(this.cities).sort()
    }
  },
  methods: {
    handleCityClick(user) {
      // this.$store.commit("changeCity", city);
      // this.changeCity(city)
      // console.log('handleCityClick')
      // console.log(user)
      this.RECORD_DETAIL_USERINFO({user})
      this.$router.push("/detail");
      
    },
    ...mapMutations(['RECORD_DETAIL_USERINFO'])
  },

  watch: {
    letter() {
      if (this.letter) {
        const element = this.$refs[this.letter][0];
        this.scroll.scrollToElement(element);
      }
    }
  },

  mounted() {
    this.scroll = new Bscroll(this.$refs.wrapper,{click: true});
  }
};
</script>

