            this.percentage = this.detailUser.get('percentage')
            this.education = this.detailUser.get('education')
            this.address = this.detailUser.get('address')
            this.person_detail = this.detailUser.get('person_detail')
            this.des = this.detailUser.get('des')
            this.target_job = this.detailUser.get('target_job')
            this.night_job = this.detailUser.get('night_job')
            this.target_salary = this.detailUser.get('target_salary')
        },
        components: {
            headTop,
            alertTip,
        },
        computed: {
            ...mapState([
                'detailUser',
            ]),
        },
        methods: {
            ...mapMutations([
                'RECORD_DETAIL_USERINFO'
            ]),
            //选择性别
            chooseSex(sex){
                this.sex = sex;
            },
            //选择性别
            chooseNight(sex){
                this.night_job = sex;
            },
            //保存地址信息
            async addAddress(){
                if(!this.name){
                    this.showAlert = true;
                    this.alertText = '请输入姓名'
                    
                }else if(!this.phone){
                    this.showAlert = true;
                    this.alertText = '请输入电话号码'
                    
                }else if(!this.idcard){
                    this.showAlert = true;
                    this.alertText = '请输入身份证'
                    
                }else{
                    // var user = this.detailUser
                    // user = updateUserParam({user,...this})
                    this.percentage = parseFloat(this.percentage)
                    var keys = ["user","name","idcard","sex","age","phone","education","address","person_detail","des","target_job","night_job","target_salary","percentage"]
                    let params =  { id: this.detailUser.id};
                    for(let i of keys){
                        if(this.detailUser.get(i) != this[i]){
                            params[i] = this[i]
                        }
                    }
                    console.log(params)
                    try {
                        const starts = await updateUser(params)
                        // await setCurrentUser(this.session)
                        this.RECORD_DETAIL_USERINFO({user:this.detailUser})
                        this.$router.go(-1);
                    } catch (error) {
                        // Show the error message somewhere and let the user try again.
                        // alert("Error: " + error.code + " " + error.message);
                        this.showAlert = true;
                        this.alertText = error.message;
                    }
                }
                
                // let res = await postAddAddress(this.userInfo.user_id, this.searchAddress.name, this.address_detail, this.geohash, this.name, this.phone, this.anntherPhoneNumber, 0, this.sex, this.tag, this.tag_type);
                // //保存成功返沪上一页，否则弹出提示框
                // if (res.message) {
                //     this.showAlert = true;
                //     this.alertText = res.message;
                // }else {
                //     this.CONFIRM_ADDRESS(1);
                //     this.$router.go(-1);
                // }
            },
        }
