<script>
    // import footGuide from '@/components/footer/footGuide'
    import headTop from '../../components/header/head'
    import datepicker from '../../components/common/datepicker.vue'
    import alertTip from '../../components/common/alertTip'
    import {formatDate,formatMonth} from '@/config/mUtils'

    import {mapState, mapMutations} from 'vuex'
    import {getRevenue,getMyChild} from '../../service/getData'

    export default {
        data(){
            return {
                freeMon:'',
                "month":formatMonth(new Date()),
                "revenue":"加载中",
                list:[],
                revenueData: null
            }
        },
        mounted(){
            this.updateView()
        },
        components: {
            headTop,
            alertTip,
            // footGuide,
            datepicker,
        },
        computed: {
            ...mapState([
            'userId',
            'login'
            ]),
            //判断手机号码
            rightPhoneNumber: function (){
                return /^1\d{10}$/gi.test(this.phoneNumber)
            },
        },
        methods: {
            ...mapMutations([
                'RECORD_USERINFO',
            ]),
             picked(year, month, date) {
                if(month < 10){
                    this.freeMon = `${year}-0${month}`;
                }else{
                    this.freeMon = `${year}-${month}`;
                }
            },
            async updateView(){
                let revenue_data = await getRevenue().first()
                let secondChild = await getMyChild().find()
                let childDict = {}
                console.log(secondChild)
                for(let child of secondChild){
                    childDict[child.id] = child
                }
                this.revenueData = revenue_data.get('list')
                console.log(this.revenueData)

                var result = [];
                // for(var i=0;i<letters.length;i++){
                for(var j in this.revenueData){
                    // if(initial == letters[i]){
                        console.log(j)
                        console.log(this.revenueData[j])
                            let familyName = ''
                            let parentsId = this.revenueData[j].parents
                            for(let p of parentsId){
                                if(childDict[p]){
                                    familyName = childDict[p].get('name')
                                }
                            }
                             result.push(
                            {
                                name:this.revenueData[j].name,
                                id:this.revenueData[j].id,
                                uptimes:this.revenueData[j].uptimes,
                                familyName,
                                calRevenue:this.revenueData[j].calRevenue ? this.revenueData[j].calRevenue.toFixed(2) : 0,
                                status:this.revenueData[j].status,
                                jobName:this.revenueData[j].jobName
                            }
                        )
                        
                       
                    }
                this.revenue = revenue_data.get('monthTotal') + "元";
                this.list = result;
                console.log(this.list)
                // }
            
            // }
            }

        }
    }

