import Parse from 'parse';
//岗位
export class Vocation extends Parse.Object {
    constructor() {
        // Pass the ClassName to the Parse.Object constructor
        super('Vocation');
        // All other initialization
        this.n = '岗位名称';
        this.des = '岗位描述'
        //日班夜班收入
        this.dincome = 50
        this.nincome = 60
        //营收
        this.revenue = 5
        //成本支出(假如有)
        this.cost = 1
        this.factory = 'factoryId'
    }
    spawn(param) {
        this.set('name',param.n)
        this.set('des',param.des)
        this.set('dincome',param.dincome)
        this.set('nincome',param.nincome)
        this.set('revenue',param.revenue)
        this.set('cost',param.cost)
    }
}


