<script>
    import headTop from '../../components/header/head'
    import {mapState, mapMutations} from 'vuex'
    import {createUser,setCurrentUser} from '../../service/getData'
    import alertTip from '../../components/common/alertTip'
    import { spawnUser } from '../../model/spawnUser';


    export default {
      data(){
            return{
                name: null, //姓名
                idcard:null,
                sex: 1, //性别
                age: '', //性别
                phone: null, //电话
                percentage:0,
                education:'',
                address:'',
                person_detail:'',
                des:'',
                target_job:'',
                night_job:1,
                target_salary:'',
                showAlert: false, //弹出框
                alertText: null, //弹出框信息
            }
        },
        created(){

        },
        components: {
            headTop,
            alertTip,
        },
        computed: {
            ...mapState([
                'userId',
                'session'
            ]),
        },
        methods: {
            ...mapMutations([
                'CONFIRM_ADDRESS'
            ]),
            //选择性别
            chooseSex(sex){
                this.sex = sex;
            },
            //选择性别
            chooseNight(sex){
                this.night_job = sex;
            },
            //保存地址信息
            async addAddress(){
                if (!this.userId) {
                    this.showAlert = true;
                    this.alertText = '请登录'
                    
                }else if(!this.name){
                    this.showAlert = true;
                    this.alertText = '请输入姓名'
                    
                }else if(!this.phone){
                    this.showAlert = true;
                    this.alertText = '请输入电话号码'
                    
                }else if(!this.idcard){
                    this.showAlert = true;
                    this.alertText = '请输入身份证'
                    
                }else{
                    this.percentage = parseFloat(this.percentage)

                    var new_user = spawnUser({...this})
                    try {
                        await new_user.signUp();
                        await setCurrentUser(this.session)
                        this.$router.go(-1);
                    } catch (error) {
                        // Show the error message somewhere and let the user try again.
                        // alert("Error: " + error.code + " " + error.message);
                        this.showAlert = true;
                        this.alertText = error.message;
                    }
                }
                
                // let res = await postAddAddress(this.userInfo.user_id, this.searchAddress.name, this.address_detail, this.geohash, this.name, this.phone, this.anntherPhoneNumber, 0, this.sex, this.tag, this.tag_type);
                // //保存成功返沪上一页，否则弹出提示框
                // if (res.message) {
                //     this.showAlert = true;
                //     this.alertText = res.message;
                // }else {
                //     this.CONFIRM_ADDRESS(1);
                //     this.$router.go(-1);
                // }
            },
        }
    }
</script>

