<script>
import footGuide from '@/components/footer/footGuide'
import headTop from '../../components/header/yhead'
import {mapState, mapMutations} from 'vuex'
import {signout} from '../../service/getData'

export default {
    data(){
        return{
            profiletitle: '我的',
            
        }
    },
    mounted(){
        this.initData();
    },
    components:{
        headTop,
        footGuide,
    },

    computed:{
        ...mapState([
            'userInfo',
        ]),
    },

    methods:{
        ...mapMutations([
            'SAVE_AVANDER'
        ]),
        initData(){
            if (this.userInfo && this.userInfo.user_id) {
                this.mobile = this.userInfo.mobile || '暂无绑定手机号';
            }else{
                this.username = '登录/注册';
                this.mobile = '暂无绑定手机号';
            }
        },
        async logout(){
            await signout()
            this.$router.replace('login')
        }
    },
    watch: {
        userInfo: function (value){
            this.initData()
        }
    }
}

